import axios from "axios";
const host = process.env.VUE_APP_HOST || "http://localhost:5000/api";

const state = {
  roomInfo: {}
};

const getters = {
  getRoomInfo: state => state.roomInfo
};

const actions = {
  async login({ commit }, params) {
    const { id } = params
    try {
      const reponse = await axios.post(`${host}/patient/register`, {
        id
      })

      if(reponse.status === 200){
        const { patient_id, room_id, interviewed } = reponse.data
        const roomInfo = {
          isLoggedIn: true,
          patient_id,
          room_id,
          interviewed
        }
        
        commit("setRoomInfo", roomInfo)
      }
      return 
    } catch (err){
      // handle error
      throw err
    }
  },
  interviewed({commit}, params) {
    commit('setInterviewed', true)
  }
};

const mutations = {
  setRoomInfo: (state, roomInfo) => (state.roomInfo = roomInfo),
  setInterviewed: (state, interviewed) => (state.roomInfo.interviewed = interviewed)
};

export default {
  state,
  getters,
  actions,
  mutations
};
