import Peer from 'skyway-js';
// import router from '../router'

const peerKey = '32a11764-ff9c-4fd7-8cae-b2ba01064625';

const timeout = ms => new Promise(res => setTimeout(res, ms))

async function delay (time) {
    await timeout(time)
}

const state = {
    peer: null,
    myStream: null,
    theirStream: null,
    dataConnection: null,
    doctorDataConnection: null,
    doctorMediaConnection: null,
    interview: false
};

const getters = {
    getPeer: state => state.peer,
    getMyStream: state => state.myStream,
    getTheirStream: state => state.theirStream,
    getDoctorDataConnection: state => state.doctorDataConnection,
    getDoctorMediaConnection: state => state.doctorMediaConnection,
    getDataConnection: state => state.dataConnection,
    getInterviewState: state => state.interview
};

const actions = {
    async initSkyway({ commit }, params) {
        const { patient_id } = params
        const peer = new Peer(`patient-${patient_id}`, {
            key: peerKey,
            debug: 3
        });
        
        peer.on('close', () => {
          
        });
        peer.on('error', err => {

        });

        await delay(500)
        commit('setPeer', peer)
        return;
    },
    async initStream({commit}, params){
        try {
            const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true })
            commit('setStream',stream)
            return stream
        } catch(err){
            console.error('mediaDevice.getUserMedia() error:', error);
            return;
        }
    },
    createConnections({dispatch, commit}, params){
        const { room_id } = this.getters.getRoomInfo;
        const stream = this.getters.getMyStream
        const peer = this.getters.getPeer

        const doctorDataConnection = this.getters.getPeer.connect(`doctor-${room_id}`);
        commit('setDoctorDataConnection', doctorDataConnection)

        peer.on('call', mediaConnection => {
            commit('setMediaConnection', mediaConnection)
            mediaConnection.answer(stream);
  
            mediaConnection.on('stream', theirStream => {
              commit('setTheirStream', theirStream)
            });
        });

        doctorDataConnection.on('open', () => {
            const data = {
                    action: 'READY',
                    payload: {
                    ...this.getters.getRoomInfo
                    }
                };
                doctorDataConnection.send(data);
        });

        peer.on('connection', (dataConnection) => {

            commit('setDataConnection', dataConnection)
            
            dataConnection.on('data', (data) => {
                console.log('data',data)
                    /*
                    if(data.action === 'READY'){
                        const data = {
                            action: 'READY',
                            payload: {
                              ...this.getters.getRoomInfo
                            }
                        };
                        const newDoctorDataConnection = this.getters.getPeer.connect(`doctor-${room_id}`);

                        newDoctorDataConnection.on('open', () => {
                            newDoctorDataConnection.send(data);
                        })
                        
                    }*/
                    if(data.action === 'INIT-READY'){
                        const data = {
                            action: 'INIT-READY',
                            payload: {
                              ...this.getters.getRoomInfo
                            }
                        };
                        const newDoctorDataConnection = this.getters.getPeer.connect(`doctor-${room_id}`);

                        newDoctorDataConnection.on('open', () => {
                            newDoctorDataConnection.send(data);
                        })
                    }
                    if(data.action === 'GO-TO-EXERCISE'){
                        dispatch('interviewed')
                        commit('setInterviewMode',false)
                    }
                    if(data.action === 'INTERVIEW'){
                        commit('setInterviewMode',true)
                    }
                    if(data.action === 'INTERVIEW-END'){
                        commit('setInterviewMode',false)
                    }
                    if(data.action === '1-ON-1'){
                        commit('setInterviewMode',true)
                    }
                    if(data.action === '1-ON-1-END'){
                        commit('setInterviewMode',false)
                    }
                    if(data.action === 'CONTROL'){
                        commit('setInterviewMode',true)
                    }
                    if(data.action === 'CONTROL-END'){
                        commit('setInterviewMode',true)
                    }                    
            })
        })
    },
    disconnect({commit}, params){
        commit('disconnectPeer')
    }
}

const mutations = {
    setPeer: (state, peer) => (state.peer = peer),
    setStream: (state, stream) => (state.myStream = stream),
    setTheirStream: (state, stream) => (state.theirStream = stream),
    setDataConnection: (state, dataConnection) => (state.dataConnection = dataConnection),
    setDoctorDataConnection: (state, doctorDataConnection) => (state.doctorDataConnection = doctorDataConnection),
    setInterviewMode: (state, mode) => (state.interview = mode),
    setMediaConnection: (state, mediaConnection) => (state.mediaConnection = mediaConnection),
    disconnectPeer: (state, params) => {
        state.myStream.getTracks()[0].stop()
        state.myStream.getTracks()[1].stop()
        if(state.peer){
            state.peer.disconnect()
            return state.peer = null
        }
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};
