<template>
<div style="margin:auto;width:600px;">
    <h1 style="margin-top:30px;"> Patient login</h1>
     <!-- Remote Heart Rehab 患者 -->
      <form @submit.prevent="onSubmit">
          <div class="row align-items-center mt-5">
              <div class="col-3 text-right">ID：</div>
                <div class="col-8 p-2">
                  <input autocomplete="id" v-model="form.id" class="form-control" type="text" placeholder="id">
                </div>
          </div>
          <div class="text-center mt-5">
              <button class="btn btn-primary" type="submit">ログイン<span class="glyphicon glyphicon-chevron-right"></span></button>
          </div>
      </form>
</div>
</template>
<script>
import { mapActions } from "vuex";

export default {
  name: "Login",
  data() {
    return {
      form: {
        id: null
      }
    };
  },
  methods: {
    ...mapActions(["login"]),
    onSubmit() {
      const { id } = this.form
      if((id && id.length === 0) || !id){
        return
      }
      
      localStorage.setItem('current_id',id)

      const userData = {
        id: id
      };
      this.login(userData)
    }
  }
};
</script>

