<template>
    <div style="padding:20px;">
        <Lobby v-if="!getRoomInfo.interviewed" />
        <Exercise v-if="getRoomInfo.interviewed" />
    </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex"
import Exercise from './Exercise'
import Lobby from './Lobby'

export default {
    name:'RoomWrapper',
    data(){
        return {

        }
    },
    components: {
            Exercise,
            Lobby
    },
    computed: mapGetters([
        "getInterviewState",
        "getTheirStream",
        "getMyStream",
        "getRoomInfo",
        "getPeer"]),
    methods: {
      ...mapActions([
        "disconnect",
        "createConnections",
        "initStream",
        "initSkyway",
        "setInterviewed"])
    },
    async created(){
        const { interviewed, room_id, patient_id } = this.getRoomInfo;

        if(!this.getPeer){
          await this.initSkyway({patient_id})
        }

        await this.initStream()
        this.createConnections()
    },
    beforeDestroy(){
        this.disconnect()
    }
}
</script>