import Vue from "vue";
import VueRouter from "vue-router";
import RoomWrapper from "../views/RoomWrapper";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "RoomWrapper",
    component: RoomWrapper
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
