<template>
  <div class="small">
    <line-chart  :styles="{
      position: 'relative',
      height: '200px'
      }" 
      :options="{
        responsive: true,
        maintainAspectRatio: false
        }" 
      :chart-data="datacollection"></line-chart>
    <!-- <button @click="fillData()">Randomize</button> -->
  </div>
</template>

<script>
  import LineChart from './LineChart.js'

  export default {
    components: {
      LineChart
    },
    data () {
      return {
        datacollection: {},
      }
    },
    mounted () {
      this.fillData()
    },
    methods: {
      fillData () {
        ["","#","#","#d62728","#9467bd","#8c564b","#e377c2","#7f7f7f","#bcbd22","#17becf"]
        this.datacollection = {
          labels: [this.getRandomInt(), this.getRandomInt()],
          datasets: [
            {
              pointBackgroundColor:'#1f77b4',
              fill:false,
              label: 'Data One',
              borderColor: '#1f77b4',
              data: [this.getRandomInt(), this.getRandomInt(),this.getRandomInt()]
            }, {
              pointBackgroundColor:'#ff7f0e',
              fill:false,
              label: 'Data One',
              borderColor: '#ff7f0e',
              data: [this.getRandomInt(), this.getRandomInt(),this.getRandomInt()]
            },
            {
              pointBackgroundColor:'#2ca02c',
              fill: false,
              label: 'Data Three',
              borderColor: '#2ca02c',
              data: [this.getRandomInt(), this.getRandomInt(),this.getRandomInt()]
            }
          ]
        }
      },
      getRandomInt () {
        return Math.floor(Math.random() * (50 - 5 + 1)) + 5
      }
    }
  }
</script>

<style>
  .small {
    margin: auto;
  }
