import axios from "axios";
const host = process.env.VUE_APP_HOST || "http://localhost:5000/api";

const state = {
    rooms: [],
    myRooms: []
};

const getters = {
    getRooms: state => state.rooms,
    getMyRooms: state => {
        return state.myRooms
    }
};

const actions = {
    async registerRoom({commit}, params) {
        const {room_id, patient_id} = params
        try {
            const reponse = await axios.post(`${host}/patient/register`,{
                room_id, 
                patient_id
            });
            const results = reponse.data;
            return
        } catch (err){
            throw err
        }
    },
    async fetchMyRooms({commit}, params) {
        const { patient_id } = params
        try {
            const reponse = await axios.get(`${host}/patient/roomspatients/${patient_id}`);
            const results = reponse.data;
            commit('setMyRooms',results.rooms)
            return true
        } catch(err){
            throw err
        }
    }
}

const mutations = {
    setRooms: (state, rooms) => (state.rooms = rooms),
    setMyRooms: (state, myRooms) => (state.myRooms = myRooms)
};

export default {
state,
getters,
actions,
mutations
};